/* Theme styles */
.theme-blue {
  --overlay-background: #1b263f;
  --overlay-background-opaque: rgba(27, 38, 63, 0.9);
  --highlight-background: #0006;
  --icon-filter: initial;
  --black-icon-filter: invert(62%) sepia(98%) saturate(12%) hue-rotate(175deg)
    brightness(100%) contrast(96%);
  --primary-color: white;
  --secondary-color: #fff9;
  --secondary-color: #fff9;
  --tertiary-color: #fff6;
  --quaternary-color: #f3eae8;
  --divider-color: #fff1;
  --primary-text-shadow: 0 0 3px #0002, 0 0 12px #0004;
  --secondary-text-shadow: 0 0 3px #0004;
  --card-color: #263659;
  --highlight-color: #0064e0;
  --secondary-button-color: #354855;
}

.theme-dark {
  --overlay-background: #202327;
  --overlay-background-opaque: rgba(32, 35, 39, 0.9);
  --highlight-background: #000d;
  --icon-filter: initial;
  --black-icon-filter: invert(62%) sepia(98%) saturate(12%) hue-rotate(175deg)
    brightness(100%) contrast(96%);
  --primary-color: white;
  --secondary-color: #fff9;
  --tertiary-color: #fff6;
  --quaternary-color: #f3eae8;
  --divider-color: #fff1;
  --primary-text-shadow: 0 0 3px #0002, 0 0 12px #0004;
  --secondary-text-shadow: 0 0 3px #0004;
  --card-color: #2e3238;
  --highlight-color: #0064e0;
  --secondary-button-color: #354855;
}

.theme-light {
  --overlay-background: #d0d0d0;
  --overlay-background-opaque: rgba(208, 208, 208, 0.9);
  --highlight-background: #b9b9b9;
  --icon-filter: brightness(0);
  --black-icon-filter: invert(39%) sepia(21%) saturate(200%) saturate(109.5%)
    hue-rotate(174deg) brightness(94%) contrast(86%);
  --primary-color: #1c1e22;
  --secondary-color: #5f6670;
  --tertiary-color: #0006;
  --quaternary-color: #f3eae8;
  --divider-color: #0001;
  --primary-text-shadow: 0 0 3px #fff2, 0 0 12px #fff4;
  --secondary-text-shadow: 0 0 3px #fff4;
  --card-color: #d9d9d9;
  --highlight-color: #0064e0;
  --secondary-button-color: #e6ebef;
}

.theme-revamp-light {
  --overlay-background: #fff;
  --overlay-background-opaque: rgba(255, 255, 255, 0.9);
  --highlight-background: #b9b9b9;
  --icon-filter: brightness(0);
  --black-icon-filter: brightness(0) saturate(100%) invert(39%) sepia(0%)
    saturate(5458%) hue-rotate(315deg) brightness(104%) contrast(86%);
  --highlighted-icon-filter: brightness(0) saturate(100%) invert(35%) sepia(29%)
    saturate(2520%) hue-rotate(192deg) brightness(95%) contrast(88%);
  --search-suggestion-icon-filter: brightness(0) saturate(100%) invert(20%)
    sepia(91%) saturate(3130%) hue-rotate(206deg) brightness(94%) contrast(101%);
  --primary-color: #050505;
  --secondary-color: #65676b;
  --tertiary-color: #0006;
  --quaternary-color: #f3eae8;
  --divider-color: #0001;
  --secondary-text-color: #4e606f;
  --primary-text-shadow: 0 0 3px #fff2, 0 0 12px #fff4;
  --secondary-text-shadow: 0 0 3px #fff4;
  --link-text-color: #0064e0;
  --link-text-filter: brightness(0) saturate(100%) invert(30%) sepia(83%)
    saturate(1285%) hue-rotate(191deg) brightness(95%) contrast(125%);
  --card-color: #fff;
  --highlight-color: #0064e0;
  --highlight-color-active: #005ebc;
  --input-background-color: #ffffff;
  --input-border-color: #ccd3db;
  --input-placeholder-color: #4e606f;
  --secondary-button-color: #e6ebef;
  --widget-background-color: #ffffff;
  --primary-icon-filter: brightness(0) invert(5%) sepia(58%) saturate(442%)
    hue-rotate(153deg) brightness(95%) contrast(97%);
  --secondary-icon-filter: brightness(0) invert(34%) sepia(17%) saturate(623%)
    hue-rotate(165deg) brightness(96%) contrast(85%);
  --meta-icon-filter: brightness(0) invert(4%) sepia(6%) saturate(5739%)
    hue-rotate(153deg) brightness(95%) contrast(94%);
  --hover-overlay-color: rgba(0, 0, 0, 0.05);
  --focus-overlay-color: rgba(0, 0, 0, 0.1);
  --tooltip-background-color: #25363f;
  --tooltip-text-color: #dde2e8;
  --divider-strong-color: #647685;
  --popover-background-color: #ffffff;
  background: linear-gradient(
      271deg,
      color(display-p3 0.9451 0.9569 0.9686 / 0) 48.31%,
      color(display-p3 0.9451 0.9569 0.9686) 89.66%
    ),
    linear-gradient(
      0deg,
      color(display-p3 1 1 1 / 0.5) 0%,
      color(display-p3 1 1 1 / 0.5) 100%
    ),
    linear-gradient(
      65deg,
      color(display-p3 0.6902 0.7804 0.9843) 14.31%,
      color(display-p3 0.6902 0.7804 0.9843 / 0) 61.22%
    ),
    linear-gradient(
      320deg,
      color(display-p3 0.7451 0.7255 0.9216) -10.2%,
      color(display-p3 0.7451 0.7255 0.9216 / 0) 55.43%
    ),
    linear-gradient(
      135deg,
      color(display-p3 0.7255 0.8667 0.9922) 6.37%,
      color(display-p3 0.7255 0.8667 0.9922 / 0) 61.12%
    ),
    color(display-p3 1 1 1);
}

.theme-revamp-dark {
  --overlay-background: #202327;
  --overlay-background-opaque: rgba(32, 35, 39, 0.9);
  --highlight-background: #000d;
  --icon-filter: initial;
  --black-icon-filter: invert(62%) sepia(98%) saturate(12%) hue-rotate(175deg)
    brightness(100%) contrast(96%);
  --search-suggestion-icon-filter: brightness(0) saturate(100%) invert(66%)
    sepia(20%) saturate(3189%) hue-rotate(184deg) brightness(103%) contrast(99%);
  --primary-color: #dde2e8;
  --secondary-color: #fff9;
  --tertiary-color: #fff6;
  --quaternary-color: #f3eae8;
  --divider-color: #fff1;
  --secondary-text-color: #a4b0bc;
  --primary-text-shadow: 0 0 3px #0002, 0 0 12px #0004;
  --secondary-text-shadow: 0 0 3px #0004;
  --link-text-color: #3e9efb;
  --link-text-filter: brightness(0) saturate(100%) invert(51%) sepia(39%)
    saturate(902%) hue-rotate(171deg) brightness(103%) contrast(97%);
  --card-color: #2e3238;
  --highlight-color: #2694fe;
  --highlight-color-active: #2178f5;
  --input-background-color: #25363f;
  --input-border-color: #647685;
  --input-placeholder-color: #a4b0bc;
  --secondary-button-color: #354855;
  --widget-background-color: #1c2b33;
  --primary-icon-filter: brightness(0) invert(96%) sepia(5%) saturate(264%)
    hue-rotate(175deg) brightness(94%) contrast(93%);
  --secondary-icon-filter: brightness(0) invert(85%) sepia(8%) saturate(445%)
    hue-rotate(169deg) brightness(84%) contrast(80%);
  --meta-icon-filter: brightness(0) invert(100%) sepia(56%) saturate(1219%)
    hue-rotate(177deg) brightness(102%) contrast(82%);
  --hover-overlay-color: rgba(255, 255, 255, 0.05);
  --focus-overlay-color: rgba(255, 255, 255, 0.1);
  --tooltip-background-color: #ffffff;
  --tooltip-text-color: #0a1317;
  --divider-strong-color: #ccd3db;
  --popover-background-color: #25363f;
  background: linear-gradient(
      271deg,
      color(display-p3 0.0392 0.0745 0.0902 / 0) 62.44%,
      color(display-p3 0.0392 0.0745 0.0902) 97.75%
    ),
    radial-gradient(
      240.83% 94.53% at 93.99% 82.17%,
      color(display-p3 0.0392 0.0745 0.0902 / 0) 0%,
      color(display-p3 0.0392 0.0745 0.0902) 100%
    ),
    linear-gradient(
      0deg,
      color(display-p3 0.0392 0.0745 0.0902 / 0.5) 0%,
      color(display-p3 0.0392 0.0745 0.0902 / 0.5) 100%
    ),
    linear-gradient(
      65deg,
      color(display-p3 0 0.2941 0.7255) 14.31%,
      color(display-p3 0 0.2941 0.7255 / 0) 61.22%
    ),
    linear-gradient(
      320deg,
      color(display-p3 0.3922 0.2549 0.8235 / 0.6) -10.2%,
      color(display-p3 0.3922 0.2549 0.8235 / 0) 55.43%
    ),
    linear-gradient(
      135deg,
      color(display-p3 0 0.451 0.6667) 6.37%,
      color(display-p3 0 0.451 0.6667 / 0) 61.12%
    ),
    color(display-p3 0.0392 0.0745 0.0902);
}

.theme-blue .overlayBackground {
  background-color: #1e2a47;
}
.theme-dark .overlayBackground {
  background-color: #27292c;
}
.theme-light .overlayBackground {
  background-color: #d6d6d6;
}
.theme-revamp-light .overlayBackground {
  background-color: #f0f2f5;
}
.theme-revamp-dark .overlayBackground {
  background-color: #27292c;
}
.theme-revamp-light .calendarWashBackground {
  background-color: #f1f4f7;
}
.theme-revamp-dark .calendarWashBackground {
  background-color: rgba(255, 255, 255, 0.1);
}

.theme-light img.icon {
  filter: brightness(0);
}

.secondaryColor {
  color: var(--secondary-color);
}

.secondaryTextColor {
  color: var(--secondary-text-color);
}

.primaryColor,
.primaryColor a {
  color: var(--primary-color);
}

.quaternaryColor {
  color: var(--quaternary-color);
}

.theme-light .overlayBackground.secondaryColor,
.theme-light .overlayBackground .secondaryColor {
  color: #9fa0a1;
}

.theme-blue .divider {
  background: #4e5d7b;
}

.theme-dark .divider {
  background: #5b5e61;
}

.theme-light .divider {
  background: #b8bcc0;
}

.theme-revamp-light .divider {
  background: #ccd3db;
  width: 1px;
  height: 86px;
}
.theme-revamp-dark .divider {
  background: #647685;
  width: 1px;
  height: 86px;
}

.theme-blue .borderColor {
  border-color: #4e5d7b;
}

.theme-dark .borderColor {
  border-color: #5b5e61;
}

.theme-light .borderColor {
  border-color: #b8bcc0;
}

/* End Theme Styles */

a {
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-rows: min-content min-content min-content 1fr min-content;
  grid-template-columns: min-content 1fr min-content;
  --toastify-color-light: var(--overlay-background);
  --toastify-text-color-light: var(--primary-color);
  transition: 0.3s;
}

body {
  min-height: 100vh;
}

#root {
  min-height: 100vh;
  overflow: auto;
}

body.searchFocused div.wrapper {
  backdrop-filter: blur(5px);
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-weight: 150;
  grid-row: 1 / 3;
  grid-column: 2;
  margin-top: 2rem;
}

.headerV2 {
  font-family: SF Pro Text, Helvetica, Arial, Noto Sans, sans-serif;
  padding: 8px 16px;
  height: 36px;
  font-size: 14px;
  line-height: 20px;
  color: var(--secondary-color);
  display: flex;
  align-items: center;
  position: fixed;
  z-index: -1;
}

.header-logo {
  filter: var(--meta-icon-filter);
  margin: 0 auto 8px auto;
}

.calendarUpNextWrapper {
  grid-column: 2;
  grid-row: 3 / 5;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 4px;
  margin-bottom: 1rem;
  gap: 1rem;
  overflow: hidden;
}

.calendarUpNextWrapperV2 {
  width: 100%;
  background-color: var(--widget-background-color);
  padding: 12px;
  border-radius: 8px;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
}

.mainContent {
  min-height: 100vh;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  padding: 52px 48px 16px;
  max-width: 1056px;
  gap: 1rem;
}

.bookmarksEnabled .mainContent {
  min-height: calc(100vh - 40px);
}

.mainContent > .topSpacing {
  flex-grow: 1;
  width: 100%;
}

.mainContent > .bottomSpacing {
  flex-grow: 3;
  width: 100%;
}

.mainContent > .searchWrapper {
  position: relative;
  min-width: auto;
  max-width: 960px;
  width: 100%;
  margin-top: 16px;
  margin-bottom: 16px;
  z-index: 2;
}

.mainContent .calendarWrapper {
  min-width: auto;
  width: 100%;
}

.time {
  margin-left: 12px;
  font-weight: 400;
  position: relative;
  justify-content: center;
  text-shadow: var(--primary-text-shadow);
  display: inline-block;
}

.timeAMPM {
  letter-spacing: 2px;
  font-weight: 400;
  margin-left: 4px;
  text-shadow: var(--primary-text-shadow);
}

.message {
  font-size: 24px;
  font-weight: 300;
  text-shadow: var(--primary-text-shadow);
}

.messageMedium {
  font-size: 40px;
  font-weight: 300;
  text-shadow: var(--primary-text-shadow);
}

.messageLarge {
  font-size: 64px;
  font-weight: 300;
  text-shadow: var(--primary-text-shadow);
}

.calendarWrapper {
  height: 90px;
  display: flex;
  justify-content: center;
  width: 100%;
  min-width: 640px;
  margin: 0px;
  flex-shrink: 0;
  flex-grow: 0;
}

.calendarWrapperV2 {
  height: 94px;
  display: flex;
  justify-content: center;
  width: 100%;
  margin: 0px;
  flex-shrink: 0;
  flex-grow: 0;
}

.diffsWrapper {
  overflow: hidden;
  flex-basis: auto;
  min-height: 25px;
}

.libraryWrapper {
  overflow: hidden;
  flex-grow: 1;
  flex-basis: 24vh;
  min-height: 200px;
  max-height: min(35vh, 400px);
}

.libraryListWrapper,
.diffTable {
  padding: 12px 4px;
  overflow-y: auto;
  scrollbar-color: var(--tertiary-color) var(--overlay-background);
}

.calendarLeftChevron {
  left: -30px;
}

.calendarRightChevron {
  right: -30px;
}

.calendarChevron {
  opacity: 0.5;
  position: absolute;
  border: 0;
  background: #0000;
  padding: 0;
  cursor: pointer;
  height: 100%;
  width: 40px;
}

.calendarChevron > img {
  width: 16px;
  height: 16px;
}

.calendarChevronV2 {
  background-color: var(--input-background-color);
  border: 0;
  border-radius: 8px;
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.1), 0px 2px 8px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  height: 28px;
  padding: 0;
  position: absolute;
  top: 33px;
  width: 32px;
  z-index: 1;
}

.calendarLeftChevronV2 {
  left: -8px;
}

.calendarRightChevronV2 {
  right: -8px;
}

.calendarWash {
  border-radius: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
}

.calendarWashV2 {
  border-radius: 8px;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
}

.hourCells {
  position: absolute;
  font-size: 10px;
  font-weight: 500;
  top: 10px;
  display: flex;
  left: 0;
  right: 0;
  justify-content: space-between;
  border-right: 1px solid #4c66a2;
}

.hourCellsV2 {
  position: absolute;
  font-size: 12px;
  font-weight: 500;
  top: 8px;
  display: flex;
  left: 0;
  right: 0;
  justify-content: space-between;
}

.hourCell {
  display: flex;
  flex-grow: 1;
  flex-basis: 0;
}

.hour {
  padding-left: 5px;
}

.hourV2 {
  padding-left: 5px;
  font-family: SF Pro Text, Helvetica, Arial, Noto Sans, sans-serif;
}

@media (max-width: 600px) {
  .hourV2 {
    display: none;
  }
}

.divider {
  background: #4e5d7b;
  width: 1px;
  height: 70px;
}

.currentTime {
  background: #d68d4a;
  width: 2px;
  height: 90px;
  position: absolute;
  pointer-events: none;
}

.currentTimeBall {
  background: #d68d4a;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  position: absolute;
  margin-left: -4px;
  margin-top: -4px;
}

.currentTimeV2 {
  background: var(--divider-strong-color);
  width: 2px;
  height: 90px;
  position: absolute;
  pointer-events: none;
}

.currentTimeBallV2 {
  background: var(--divider-strong-color);
  width: 10px;
  height: 10px;
  border-radius: 50%;
  position: absolute;
  margin-left: -4px;
  margin-top: -4px;
}

.upNextWrapper.upNextWrapperHidden {
  visibility: hidden;
}

.upNextWrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  margin: 0px 0px;
}

.upNext {
  font-size: 14px;
  line-height: 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-weight: 300;
  min-width: 640px;
  max-width: 640px;
  height: 61px;
  background-color: var(--overlay-background);
  font-family: 'Roboto';
  font-style: normal;
  border-radius: 0px 0px 10px 10px;
}

.upNextV2 {
  font-size: 14px;
  line-height: 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-weight: 300;
  max-width: 960px;
  width: 100%;
  background-color: var(--widget-background-color);
  font-family: SF Pro Text, Helvetica, Arial, Noto Sans, sans-serif;
  font-style: normal;
  border-radius: 0px 0px 10px 10px;
  padding: 8px 0 0 8px;
}

.upNextDetailsContainer {
  flex-grow: 4;
  padding: 12px;
}

.upNextDocumentsContainer {
  flex-grow: 3;
  padding: 0 2px;
  display: flex;
  align-items: center;
  justify-content: end;
  font-weight: 400;
  font-size: 14px;
}

.upNextDocumentsContainerV2 {
  flex-grow: 3;
  display: flex;
  align-items: center;
  font-weight: 400;
  font-size: 14px;
}

@media (max-width: 760px) {
  .upNextDocumentsContainerV2 {
    display: none;
  }
}

.upNextDocumentLinkContainer {
  padding: 4px 8px;
}

.upNextJoinMeetingContainer {
  padding: 0 12px 0 4px;
}

.calendarButtonsContainer {
  display: flex;
  flex-direction: row;
  justify-content: 'flex-end';
  align-items: center;
  gap: 8px;
}

.upNextLabel {
  font-weight: 350;
}

.upNextTitle {
  font-weight: 500;
  max-width: 300px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.upNextTitleV2 {
  font-weight: 700;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  display: block;
}

.upNextLabelV2 {
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
}

.earlyMeetingWrapper.earlyMeetingWrapperHidden {
  opacity: 0.8;
}

.earlyMeetingWrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 0px;
  align-items: center;
}

.earlyMeeting.earlyMeetingHidden {
  height: 0;
}

.earlyMeeting {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-width: 640px;
  height: 32px;
  background-color: var(--overlay-background);
  padding: 4px 0px;
  border-radius: 10px 10px 0px 0px;
}

.earlyMeetingLabel.earlyMeetingLabelHidden {
  visibility: hidden;
}

.earlyMeetingLabel {
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 1px;
  font-weight: 500;
}

.event {
  position: absolute;
  height: 40px;
  top: 30px;
  background: #5374b8;
  border-radius: 5px;
  opacity: 0.8;
  box-sizing: border-box;
  margin: 1px;
  border: 1px solid transparent;
}

.event:hover {
  border: 1px solid #ffffff80;
  opacity: 1;
}

.eventTentative {
  background-image: repeating-linear-gradient(
    45deg,
    rgba(255, 255, 255, 0.2),
    rgba(255, 255, 255, 0.2) 6px,
    transparent 6px,
    transparent 8px
  );
}

.eventFree {
  background-image: linear-gradient(
    to bottom,
    rgba(255, 255, 255, 0.5),
    rgba(255, 255, 255, 0.5)
  );
}

.eventPopover {
  border: 1px solid #ffffff5f;
  border-radius: 8px;
  opacity: 1;
  padding: 12px;
  font-size: 14px;
}

.tippyPopover.tippy-box {
  background: unset;
}

.tippyPopover .tippy-content {
  padding: 0;
}

.tippyPopover .tippy-arrow {
  color: #ffffff5f;
}

.eventPurple {
  background-color: #8c6ece;
  opacity: 0.6;
}

.eventDarkPurple {
  background-color: #58409b;
  opacity: 0.6;
}

.eventTeal,
.eventDarkTeal {
  background-color: #4cc6ee;
  opacity: 0.6;
}

.eventOlive,
.eventGreen,
.eventDarkGreen,
.eventDarkOlive {
  background-color: #a4d171;
  border-color: #a4d171;
  opacity: 0.6;
}

.eventRed,
.eventDarkRed,
.eventDarkMaroon {
  background-color: #f55166;
  border-color: #f55166;
  opacity: 0.6;
}

.eventMaroon {
  background-color: #ec7ebd;
  border-color: #ec7ebd;
