/* Hide things when searching */
.searchFocused .message,
.searchFocused .time,
.searchFocused .upNextWrapper,
.searchFocused .libraryWrapper,
.searchFocused .earlyMeeting,
.searchFocused .calendarWrapper,
.searchFocused .diffsWrapper {
  visibility: hidden;
  transition: all 0.25s ease-in-out;
}

.searchWrapper {
  opacity: 1;
  transition: all 0.25s ease-in-out;
  min-width: 350px;
}

.searchFocused .searchWrapper {
  margin-top: 0;
  position: fixed;
  top: 10%;
  transition: all 0.4s ease-in-out;
}

.searchWithSuggestions {
  position: relative;
  z-index: 1;
  transition: all 0.4s ease-in-out;
}

.searchFocused .searchHasText {
  background: var(--overlay-background);
  padding: 4px 24px 0px 24px;
  border-radius: 24px;
  transition: all 0.4s ease-in-out;
}

.searchResultHighlight {
  font-weight: 500;
}

.searchCloseButton {
  display: none;
  z-index: 2;
  position: absolute;
  top: 32px;
  right: 32px;
}

.searchCloseButtonV2 {
  z-index: 2;
  position: absolute;
  top: 24px;
  right: 16px;
}

.searchFocused .searchCloseButton {
  display: block;
}

.switch {
  position: relative;
  display: block;
  float: right;
  left: 0px;
  width: 36px;
  height: 20px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #6664;
  transition: 0.4s;
  border: 1px solid white;
  border-radius: 12px;
}

.slider:before {
  position: absolute;
  content: '';
  height: 12px;
  width: 12px;
  left: 3px;
  bottom: 3px;
  background-color: white;
  box-shadow: 0 0 3px #0006;
  transition: 0.4s;
  border-radius: 50%;
}

input:checked + .slider {
  background-color: rgb(21, 101, 192);
}

input:focus + .slider {
  box-shadow: 0 0 3px #0002;
}

input:checked + .slider:before {
  transform: translateX(16px);
}

